/* user header css start */
.user-header 
  position: fixed
  top: 0 
  left: 0 
  @extend %w-100 
  @extend %d-flex 
  justify-content: space-between
  @extend %align-center 
  padding: rem(10px) rem(30px)
  @extend %base-color-two 
  z-index: 2
  .sp_site_logo 
    opacity: 0 
    visibility: hidden
    +media(1199px)
      opacity: 1 
      visibility: visible 

.sidebar-toggeler 
  display: none 
  +media(1199px)
    display: inline-flex
    background-color: $body-bg 
    color: #fff 
    border: 1px solid $border-color 
    width: 40px 
    height: 35px 
    justify-content: center
    align-items: center
    font-size: rem(20px)

.user-dropdown 
  +media(1199px)
    display: none 
  .user-btn 
    background-color: transparent 
    &::after 
      border-top-color: #fff 
    img 
      width: 35px 
      height: 35px 
      @extend %bs-50 
      @extend %obj-fit 
      margin-right: rem(6px)
    span 
      @extend %text-white 

.dropdown-menu 
  @extend %base-color-two 
  border: 1px solid $border-color 
  .dropdown-item 
    @extend %text-white 
    font-size: rem(15px)
    &:hover 
      background-color: $body-bg 
/* user header css end */


/* user sidebar css start */
.user-sidebar 
  position: fixed
  top: 0  
  left: 0 
  width: 270px 
  height: 100vh
  z-index: 3
  @extend %base-color-two 
  @extend %transition
  &.active 
    left: 0
  +media(1199px) 
    left: -100% 
    border-right: 1px solid $border-color 
  .sp_site_logo 
    padding: rem(10px) rem(20px)
    margin-bottom: rem(30px)

.sidebar-menu
  max-height: calc(100vh - 150px)
  overflow-y: auto 
  .menu-caption 
    font-size: rem(14px) 
    color: #9d9d9d
    padding: rem(6px) rem(20px)
    font-weight: 500 
    &.border-top 
      border-top-color: lighten($border-color, 20%) !important
      padding-top: rem(20px)
      margin-top: rem(15px) 
  li 
    a 
      @extend %d-flex 
      @extend %align-center 
      color: #c0c0c0
      padding: rem(10px) rem(25px) 
      border-left: 3px solid transparent 
      font-size: rem(15px) 
      @extend %transition 
      i 
        font-size: rem(19px)
      &.active 
        @extend %text-base 
        background-color: $body-bg 
        border-left-color: $base-color 
      &:hover 
        @extend %text-base 

.sidebar-menu
  scrollbar-width: thin
  scrollbar-color: rgba(#fff, 0.05) $base-color-two
  &::-webkit-scrollbar
    width: 12px
  &::-webkit-scrollbar-track 
    background: $base-color-two
  &::-webkit-scrollbar-thumb
    background-color: rgba(#fff, 0.05)
    border-radius: 20px
    border: 3px solid $base-color-two 


/* dashboard main css start */
.dashbaord-main 
  padding: rem(100px) rem(30px) rem(30px) rem(300px)
  +media(1199px) 
    padding-left: rem(30px)
  +media(575px)
    padding: rem(100px) rem(20px) rem(20px) rem(20px)

.dashboard-top 
  margin-bottom: rem(40px)

.d-card 
  @extend %position-relative 
  padding: rem(25px)
  @extend %base-color-two 
  @extend %bs-8 
  border: 1px solid $border-color 
  @extend %overflow-hidden 
  @extend %transition 
  &:hover 
    @extend %trans-y 
    border-color: $base-color 
  i 
    @extend %position-absolute 
    bottom: -18px 
    right: -18px 
    font-size: 90px 
    line-height: 1
    opacity: 0.1 
    @extend %text-base 
  .d-card-amount 
    margin-top: rem(15px)
    +media(1550px)
      font-size: rem(24px)
/* dashboard main css end */