/* latest user section css start */
.single_transaction 
  padding: rem(15px) 
  background-color: $base-color-two 
  border: 1px solid rgba(#fff, 0.1)
  @extend %bs-10 
  @extend %d-flex 
  &_thumb 
    width: 60px 
    height: 60px 
    @extend %bs-5 
    @extend %overflow-hidden 
    img 
      @extend %w-100 
      @extend %h-100 
      object-fit: cover
      -o-object-fit: cover
      object-position: top
      -o-object-position: top 
  &_content 
    width: calc(100% - 60px)
    padding-left: rem(15px)

.sp_transaction_slider
  .slick-list 
    margin: rem(-10px) 
  .sp_transaction_slide 
    padding: rem(10px)
/* latest user section css end */