/* testimonial section css start */
.sp_testimonial_section 
  @extend %position-relative 
  @extend %z-index-p 
  overflow: hidden 
  .testimonial_el 
    @extend %position-absolute 
    bottom: 0 
    left: 50% 
    +transform(translateX(-50%)) 
    @extend %z-index-c 
    opacity: 0.15
    img 
      max-width: 600px 
      +media(575px)
        max-width: 300px 

.sp_testimonial_wrapper 
  background-color: $base-color-two 
  padding: rem(80px) rem(50px) rem(60px) rem(50px) 
  +border-radius(15px)
  @extend %position-relative 
  @extend %z-index-p
  +media(991px) 
    padding: rem(60px) rem(40px) rem(50px) rem(40px)
  +media(575px)
    padding: rem(60px) rem(20px) rem(30px) rem(20px)
  &_icon  
    @extend %position-absolute 
    top: -40px 
    left: 50px 
    width: 80px 
    height: 80px 
    @extend %base-color 
    @extend %bs-50 
    @extend %d-flex 
    @extend %align-center 
    @extend %justify-center 
    box-shadow: 0 0 5px rgba($base-color, 0.45)
    +media(991px)
      top: -30px
      left: 50% 
      width: 60px 
      height: 60px
      margin-left: -30px 
      padding: 10px  

.sp_testimonial_item 
  @extend %position-relative 
  background-color: darken($base-color-two, 2%)
  padding: rem(25px) 
  @extend %bs-10 
  i 
    @extend %position-absolute 
    bottom: 35px 
    right: 30px 
    color: #fff 
    font-size: rem(42px) 
    line-height: 1 
    opacity: 0.1
  .sp_client 
    @extend %d-flex
    @extend %align-center  
    margin-top: rem(25px)
    &_thumb 
      width: 60px 
      height: 60px 
      @extend %overflow-hidden 
      @extend %bs-50 
      img 
        @extend %w-100 
        @extend %h-100 
        @extend %obj-fit 
    &_content 
      padding-left: rem(15px) 

.testimonial_slider 
  .slick-list 
    margin: 0 rem(-15px) 
  .testimonial_single 
    padding: 0 rem(15px)

.testimonial_slider_nav 
  button 
    @extend %text-base 
    font-size: rem(22px) 
/* testimonial section css end */