/* footer section css start */
.sp_footer_section 
  @extend %position-relative
  @extend %z-index-p 
  padding-top: rem(120px)
  +media(1399px)
    padding-top: rem(150px)
  +media(1199px)
    padding-top: rem(120px)
  +media(991px)
    padding-top: rem(100px)
  +media(575px)
    padding-top: rem(70px)
  .footer-main 
    padding: rem(60px) 0
  .footer-bottom
    padding: rem(20px) 0 
    border-top: 1px solid $border-color 
  .sp_footer_el
    @extend %position-absolute 
    bottom: 0
    left: 0 
    @extend %w-100 
    @extend %z-index-c
    opacity: 0.25 

.footer_info_item
  @extend %d-flex 
  @extend %align-center
  &_icon 
    width: 45px 
  &_content 
    width: calc(100% - 45px) 
    padding-left: rem(10px) 
    a
      @extend %text-p 
      &:hover 
        @extend %text-base 

.subscribe-form 
  @extend %d-flex 
  .form-control 
    width: calc(100% - 145px)
    +transform(translateX(5px))
  .btn
    width: 145px
    position: relative
    z-index: 1
    +border-radius(0 5px 5px 0)

.sp_site_logo 
  img 
    max-height: 55px 
    max-width: 215px

.footer-item 
  .footer-item-title 
    margin-bottom: rem(20px) 

.footer-menu 
  li + li 
    margin-top: rem(8px) 
  li 
    a 
      @extend %text-p 
      &:hover 
        @extend %text-base 

.footer-contact-list 
  li + li 
    margin-top: rem(10px) 
  li 
    @extend %d-flex 
    i 
      width: 45px 
      font-size: rem(17px)
    p 
      width: calc(100% - 45px)
      a 
        @extend %text-p 
        &:hover 
          @extend %text-base 

.f-social-list 
  @extend %d-flex 
  @extend %align-center 
  margin: rem(-5px) rem(-8px) 
  li 
    a 
      @extend %text-p 
      padding: rem(5px) rem(8px)
      &:hover 
        @extend %text-base 
/* footer section css end */