/* form css start */
.form-group 
  margin-bottom: rem(15px) 
  
.form-control
  padding: rem(10px) rem(15px)
  border: 1px solid $border-color
  width: 100%
  background-color: transparent 
  @extend %bs-5 
  @extend %text-h 
  height: rem(50px) 
  +placeholder(#a0a0a0) 
  @extend %transition 
  &:focus 
    background-color: transparent 
    border-color: $base-color !important 
    box-shadow: 0 0 5px rgba($base-color, 0.35) 
    @extend %text-h 
    +placeholder(transparent)
  &[readonly] 
    background-color: rgba($base-color, 0.1)
  &.form-control-md 
    height: 45px
  &.form-control-sm
    height: 35px

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus 
  -webkit-text-fill-color: $heading-color
  transition: background-color 5000s ease-in-out 0s 

.custom-icon-field 
  position: relative
  i 
    position: absolute
    top: 0
    left: 0 
    width: 45px
    @extend %h-100 
    @extend %d-flex 
    @extend %align-center 
    @extend %justify-center 
  i[class*="la-"] 
    font-size: rem(24px)
    color: lighten($para-color, 40%)
  .form-control 
    padding-left: rem(45px)
    &:focus ~ i 
      border-color: $base-color 
      @extend %text-base

.select 
  padding: rem(10px) rem(15px)
  width: 100%
  border: 1px solid darken($border-color, 5%)
  cursor: pointer
  color: $para-color
  @extend %base-color-two 
  height: rem(50px)
  +border-radius(4px)
  @extend %text-h
  option
    padding: rem(10px) 0
    display: block
    border-top: 1px solid $border-color 
  &.select--sm 
    height: rem(35px)
    font-size: rem(14px)
    padding: rem(5px)
textarea 
  min-height: rem(150px) !important
  resize: none
  width: 100%
label 
  color: lighten(#fff, 30%)
  margin-bottom: rem(8px) 
  font-size: rem(14px)
  font-weight: 500
.input-group>.form--control, 
.input-group>.select
  position: relative
  flex: 1 1 auto
  width: 1%
  min-width: 0
.input-group 
  select 
    background-color: transparent 
    border: none
  .input-group-text 
    @extend %base-color-two 
    border-color: $body-bg 
    @extend %text-white 

.site-radio 
  position: relative
  padding-left: 0
  input[type=radio] 
    width: 100% 
    position: absolute
    top: 0 
    left: 0 
    visibility: hidden
    cursor: pointer
    &:checked 
      ~ label
        &::before 
          border-width: 2px
          border-color: $base-color 
        &::after 
          opacity: 1
  label 
    margin-bottom: 0 
    position: relative
    padding-left: 20px 
    font-size: rem(14px)
    font-weight: 400
    &::before 
      position: absolute
      content: '' 
      top: 4px
      left: 0 
      width: 15px 
      height: 15px 
      border: 1px solid #888888
      +border-radius(50%)
      +transition(all 0.3s)
    &::after   
      position: absolute
      content: '' 
      top: 8px
      left: 4px
      width: 7px
      height: 7px
      background-color: $base-color
      +border-radius(50%)
      opacity: 0 
      +transition(all 0.3s)

.site-checkbox 
  padding-left: rem(25px)
  input 
    display: none
    &:checked
      ~ label 
        &::before 
          content: "\f14a"
          color: $base-color
          border-color: transparent 
  label 
    position: relative
    font-size: rem(15px)
    font-weight: 400
    cursor: pointer
    margin-bottom: 0
    &::before 
      position: absolute
      content: "\f0c8"
      font-family: "Font Awesome 5 Free"
      font-weight: 900
      top: 3px
      left: rem(-25px)
      font-size: rem(20px)
      line-height: 1
      @extend %transition 
      color: $base-color-two 
      border: 1px solid $border-color 
      @extend %bs-5 
/* form css end*/