.btn 
  padding: rem(12px) rem(30px)
  font-weight: 400 
  +border-radius(8px) 
  border: none
  font-size: rem(16px)
  box-shadow: 0 0 3px #000 
  font-size: rem(18px)
  &.btn-md
    padding: rem(10px) rem(20px)
  &.btn-sm 
    padding: rem(8px) rem(20px)

.main-btn 
  @extend %base-color 
  @extend %text-white
  @extend %d-inline-flex 
  @extend %justify-center 
  @extend %align-center 
  i.las 
    font-size: rem(18px)
  &:hover 
    @extend %text-white 

.main-btn2
  @extend %base-color-two
  @extend %text-white
  @extend %d-inline-flex 
  @extend %align-center 
  i.las 
    font-size: rem(18px)
  &:hover 
    @extend %text-white 

.grad-btn 
  @extend %bg-grad 
  @extend %text-white 
  &:hover 
    // background-size: 150% 200%
    @extend %text-white 

.grad-border-btn 
  @extend %position-relative 
  @extend %bg-grad 
  @extend %text-white 
  @extend %z-index-p 
  +border-radius(8px) 
  &:hover 
    @extend %text-base 
  &::after 
    @extend %position-absolute 
    content: '' 
    top: 1px
    left: 1px 
    width: calc(100% - 2px)
    height: calc(100% - 2px)
    background-color: $body-bg 
    +border-radius(8px) 
    @extend %z-index-c 

.view-btn 
  width: 35px 
  height: 35px 
  @extend %base-color 
  @extend %text-white 
  @extend %bs-5 
  @extend %d-inline-flex 
  @extend %align-center 
  @extend %justify-center 
  &:hover 
    @extend %text-white 

.rotate-arrow 
  +transform(rotate(-45deg))