/* why choose section css start */
.sp_choose_section 
  @extend %position-relative 
  @extend %z-index-p
  .sp_choose_section_wave 
    @extend %position-absolute 
    bottom: 0 
    left: 0 
    opacity: 0.25 
    @extend %z-index-c 

.sp_choose_item
  @extend %d-flex 
  &_icon 
    width: 60px
  &_content 
    width: calc(100% - 60px) 
    padding-left: rem(25px) 

.sp_choose_thumb 
  display: inline-block 
  @extend %position-relative 
  &::after 
    @extend %position-absolute 
    content: '' 
    bottom: 10px
    left: 50%
    +transform(translateX(-50%))
    width: 215px
    height: 68px
    @extend %bs-50 
    box-shadow: 0 22px 35px rgba($base-color, 0.55)
/* why choose section css end */