/* roadmap section css start */
.sp_roadmap_slider
  @extend %position-relative 
  @extend %z-index-p 
  padding: 0 rem(60px) 
  &::after 
    @extend %position-absolute 
    content: '' 
    top: 50% 
    left: 30px 
    width: calc(100% - 60px) 
    height: 1px
    @extend %base-color 
    @extend %z-index-c 
  .slick-arrow
    @extend %position-absolute
    top: 50% 
    +transform(translateY(-50%))
    width: 30px 
    height: 30px 
    @extend %bs-50 
    border: 2px solid var(--main-color) 
    background-color: $body-bg 
    z-index: 1 
    box-shadow: 0 0 4px 6px $body-bg 
    @extend %text-base 
    @extend %d-flex 
    @extend %align-center 
    @extend %justify-center 
    &.prev 
      left: 30px 
    &.next 
      right: 30px 

.sp_roadmap_single
  @extend %text-center 
  width: calc(100% / 4)
  min-height: 220px
  display: flex !important
  flex-flow: column 
  justify-content: flex-end 
  @extend %position-relative 
  &:nth-child(even)
    justify-content: flex-start  
    .roadmap-caption 
      order: -1 
    .roadmap-dot 
      .roadmap-line 
        bottom: auto 
        top: -30px 
  .roadmap-dot 
    @extend %position-absolute 
    left: 50% 
    top: 50% 
    margin-left: -15px 
    margin-top: -15px 
    width: 30px 
    height: 30px 
    @extend %bs-50 
    border: 2px solid var(--main-color) 
    background-color: $body-bg 
    z-index: 1 
    box-shadow: 0 0 4px 6px $body-bg 
    &::before 
      @extend %position-absolute  
      content: '' 
      top: 50% 
      left: 50% 
      margin-left: -5px 
      margin-top: -5px 
      width: 10px 
      height: 10px 
      @extend %base-color 
      @extend %bs-50 
    .roadmap-line 
      @extend %position-absolute 
      bottom: -30px 
      left: 50% 
      width: 1px 
      height: 30px 
      @extend %base-color 
  .roadmap-date 
    font-size: rem(18px) 
    font-weight: 400 
    @extend %text-white 
  .roadmap-caption 
    color: darken(#fff, 30%) 

/* roadmap section css end */