.check-list 
  li + li 
    margin-top: rem(10px) 
  li 
    font-size: rem(18px) 
    @extend %position-relative 
    padding-left: rem(30px)
    &::before 
      @extend %position-absolute 
      content: "\f00c"
      top: 2px
      left: 0 
      font-family: "Font Awesome 5 Free"
      font-weight: 900
      font-size: rem(16px)
      @extend %text-base 
      width: 25px 
      height: 25px
.number-list 
  list-style: decimal
  padding-left: rem(18px)
  li + li 
    margin-top: rem(10px)

.disc-list 
  li + li 
    margin-top: rem(10px)
  li 
    position: relative
    padding-left: rem(15px)
    &::before 
      position: absolute
      content: '' 
      top: 50%
      left: 0 
      width: rem(6px) 
      height: rem(6px)
      margin-top: rem(-3px)
      +border-radius(50%)
      background-color: #575757

.caption-list 
  li 
    display: flex 
    flex-wrap: wrap
    padding: rem(10px) 0
    font-size: rem(15px)
    border-bottom: 1px dashed $border-color
    &:first-child
      padding-top: 0
    &:last-child
      padding-bottom: 0
      border-bottom: none
    .caption 
      width: 40%
      font-family: $heading-font
      font-weight: 700
      font-size: rem(14px)
      position: relative
      @extend %text-h
      &::after 
        position: absolute
        content: ':'
        top: 0
        right: 0
    .value 
      width: 60%
      padding-left: rem(15px)
.caption-list-two 
  padding: rem(10px) rem(15px)
  background-color: rgba($base-color, 0.1)
  +border-radius(5px)
  li
    font-family: $heading-font
    font-weight: 500 
    color: $heading-color 
    font-size: rem(14px)
    padding: rem(8px) 0
    display: flex
    flex-wrap: wrap
    align-items: center
    border-bottom: 1px dashed darken($border-color, 10%)
    &:first-child
      padding-top: 0
    &:last-child
      padding-bottom: 0 
      border-bottom: none
    .caption 
      width: 20%
      position: relative
      font-weight: 700
      padding-right: 10px
      &::after 
        position: absolute
        content: ':'
        top: 0
        right: 0
    .value 
      width: 80%
      padding-left: 20px