/* overview section css start */
.sp_video_wrapper 
  height: 550px 
  @extend %overflow-hidden 
  @extend %bs-10 
  @extend %position-relative 
  @extend %z-index-p 
  &::after 
    @extend %position-absolute 
    content: '' 
    top: 0 
    left: 0 
    @extend %w-100 
    @extend %h-100 
    background-color: $body-bg 
    opacity: 0.65
  .sp_play_btn 
    @extend %position-absolute 
    top: 37% 
    left: 50% 
    width: 90px
    height: 90px
    margin-left: -45px 
    margin-top: -45px 
    z-index: 2 
    @extend %transition 
    +media(991px)
      top: 38% 
    +media(767px)
      top: 30% 
      width: 60px 
      height: 60px 
    &:hover 
      +transform(scale(1.05, 1.05))
  img 
    @extend %w-100 
    @extend %h-100 
    object-fit: cover
    -o-object-fit: cover
    object-position: top 
    -o-object-position: top 

.sp_overview_wrapper 
  @extend %position-absolute 
  bottom: 0 
  left: 0
  @extend %w-100 
  padding: rem(50px) rem(40px) 
  z-index: 1
  background: rgba(255, 255, 255, 0.1)
  backdrop-filter: blur(6.5px)
  -webkit-backdrop-filter: blur(6.5px)
  +media(1399px)
    padding: rem(30px) rem(40px) 
  +media(991px)
    padding: rem(20px) 


.sp_overview_items 
  div[class*="col-"]
    @extend %position-relative 
    &:last-child
      &::after 
        display: none 
    &:nth-child(2)
      &::after 
        +media(991px)
          display: none 
    &::after 
      @extend %position-absolute 
      content: '' 
      top: 50% 
      right: 0
      width: 1px 
      height: 60px 
      margin-top: -30px
      background-color: #fff 
      opacity: 0.15 

.sp_overview_item 
  @extend %d-flex 
  @extend %align-center 
  // @media (min-width: 768px) and (max-width: 991px)  
  +media(991px) 
    text-align: center 
    justify-content: center 
  &_icon 
    width: 60px
    +media(1399px) 
      width: 50px 
    +media(991px) 
      width: 40px 
  &_content 
    width: calc(100% - 60px)
    padding-left: 18px
    +media(1399px)  
      width: calc(100% - 50px) 
    // @media (min-width: 768px) and (max-width: 991px)
    +media(991px) 
      width: 100%
      padding-left: 0 
      margin-top: rem(15px)
    .sp_overview_item_amount 
      +media(1399px) 
        font-size: rem(24px) 
      +media(991px) 
        font-size: rem(20px) 
    p 
      +media(1399px) 
        font-size: rem(14px) 
/* overview section css end */