.sp_accordion
  .accordion-item 
    border: none 
    border-bottom: 1px solid $border-color
    @extend .sp_separator_bg 
    &:last-child
      border-bottom: none 
    .accordion-header 
      .accordion-button 
        @extend %position-relative
        font-size: rem(20px) 
        box-shadow: none 
        padding: rem(20px) rem(20px)
        @extend .sp_separator_bg 
        @extend %text-h 
        font-weight: 500 
        &:not(.collapsed) 
          @extend %text-base 
          border-bottom: 1px solid rgba(#fff, 0.1)
          &::after 
            +transform(rotate(0)) 
        &::after 
          @extend %position-absolute 
          top: 20px 
          right: 15px 
          content: "\f067" 
          background-image: none 
          font-family: 'Line Awesome Free' 
          font-weight: 900 
    .accordion-body 
      padding: rem(20px) 