/* blog section css start */
.sp_blog_post 
  @extend %bs-8 
  @extend %overflow-hidden
  @extend %transition 
  @extend %d-flex 
  position: relative 
  z-index: 1 
  +media(1199px)
    display: block
  &:hover 
    box-shadow: 0 5px 20px rgba(#000, 0.05)
  &_thumb  
    @extend %overflow-hidden 
    width: 240px
    +media(1199px) 
      width: 100% 
      height: 220px
    img 
      @extend %w-100 
      @extend %h-100 
      @extend %obj-fit 
  &_content 
    @extend %base-color-two 
    width: calc(100% - 240px)
    padding: rem(25px)
    +media(1199px)  
      width: 100%
    .title 
      font-size: rem(24px) 
      +media(575px)
        font-size: rem(22px)
      a 
        display: -webkit-box
        -webkit-line-clamp: 2
        -webkit-box-orient: vertical
        overflow: hidden
        text-overflow: ellipsis
    p 
      display: -webkit-box
      -webkit-line-clamp: 4
      -webkit-box-orient: vertical
      overflow: hidden
      text-overflow: ellipsis
      padding-top: rem(15px) 
      margin-top: rem(15px)
      border-top: 1px solid rgba(#fff, 0.1) 
    .sp_blog_meta
      padding-top: rem(15px) 
      margin-top: rem(15px)
      border-top: 1px solid rgba(#fff, 0.1) 


.sp_blog_meta
  @extend %d-flex 
  @extend %align-center 
  margin: rem(-3px) rem(-10px)
  li 
    padding: rem(3px) rem(10px)

.blog-btn 
  @extend %d-flex 
  @extend %align-center 
  justify-content: space-between 
  padding-top: rem(15px)
  margin-top: rem(25px)
  border-top: 1px solid rgba(#fff, 0.1)
  @extend %text-p
/* blog section css end */


/* blog details section css start */
.blog-details-img 
  @extend %bs-10 
  @extend %overflow-hidden 
  max-height: 500px 
  img 
    @extend %w-100 
    @extend %h-100 

.blog-widget 
  padding: rem(25px) 
  @extend %base-color-two 
  @extend %bs-8 
  .blog-widget-title 
    margin-bottom: rem(15px)

.short-post-wrapper 
  .short-post 
    padding: rem(20px) 0
    border-bottom: 1px solid $border-color
    &:last-child
      padding-bottom: 0 
      border-bottom: none 

.short-post 
  @extend %d-flex 
  .thumb
    width: 80px 
    height: 55px 
    @extend %overflow-hidden 
    +media(1199px)
      width: 65px
      height: 50px 
    img 
      @extend %w-100 
      @extend %h-100 
      @extend %obj-fit 
  .content 
    width: calc(100% - 80px) 
    padding-left: rem(20px)
    +media(1199px) 
      width: calc(100% - 65px) 
    .title 
      a 
        font-size: rem(18px)
        display: -webkit-box
        -webkit-line-clamp: 2
        -webkit-box-orient: vertical
        overflow: hidden
        text-overflow: ellipsis 
/* blog details section css end */ 