/* contact section css start */
.sp_contact_list
  li + li 
    margin-top: rem(15px) 
  li 
    @extend %d-flex 
    @extend %align-center 
    i 
      width: 40px
      @extend %text-white
    p 
      width: calc(100% - 40px) 
      @extend %text-white 
      a 
        @extend %text-white 
        &:hover 
          @extend %text-base 

.contact_social 
  @extend %d-flex 
  margin: 0 rem(-15px) 
  li 
    a 
      padding: 0 rem(15px) 
      @extend %text-white 
      &:hover 
        @extend %text-base 
/* contact section css end */ 