/* team section css start */
.sp_team_item
  @extend %text-center 
  padding: 0 30px 30px 30px 
  @extend %position-relative 
  @extend %z-index-p 
  &:hover 
    .sp_team_item_img 
      @extend %trans-y 
      box-shadow: 0 10px 25px rgba($base-color, 0.25)
  &::before 
    @extend %position-absolute 
    content: '' 
    top: 120px 
    left: 0 
    width: 100% 
    height: calc(100% - 120px)
    @extend .sp_separator_bg 
    @extend %bs-10 
    @extend %z-index-c
  &_img 
    height: 230px 
    @extend %bs-5 
    @extend %overflow-hidden 
    @extend %transition 
    img 
      @extend %w-100 
      @extend %h-100 
      @extend %obj-fit 
  &_content 
    margin-top: rem(20px)
  .social-media 
    @extend %d-flex 
    @extend %align-center 
    @extend %justify-center 
    margin: rem(10px) rem(-12px) 0 rem(-12px)
    li 
      a 
        @extend %text-white 
        padding: 0 rem(12px)
        font-size: rem(14px) 
        &:hover 
          @extend %text-base 

.sp_team_slider 
  overflow: hidden
  .slick-list 
    margin: 0 rem(-20px)
  .sp_team_slide 
    padding: 0 rem(20px)
  .slick-dots 
    @extend %d-flex 
    @extend %align-center 
    @extend %justify-center 
    margin-top: rem(20px) 
    li 
      margin: 0 7px
      &.slick-active 
        button 
          background-color: $base-color 
          &::before 
            opacity: 1
      button 
        font-size: 0 
        width: 6px 
        height: 6px 
        background-color: #fff 
        @extend %bs-50 
        @extend %position-relative
        @extend %transition
        &::before
          @extend %position-absolute
          content: '' 
          top: -5px 
          left: -5px
          width: 16px 
          height: 16px 
          border: 1px solid $base-color  
          @extend %bs-50 
          opacity: 0 
          @extend %transition

/* team section css end */