/* referral section css start */
.sp_referral_wrapper 
  div[class*="col-"]
    &:last-child
      .sp_referral_item 
        &::before,
        &::after 
          display: none 
    &:nth-child(3)
      .sp_referral_item 
        &::before,
        &::after 
          @media (min-width: 576px) and (max-width: 1199px)
            display: none 
    &:nth-child(2n + 2)
      .sp_referral_item 
        &::before,
        &::after 
          +media(575px)
            display: none 
      

.sp_referral_item
  @extend %text-center 
  @extend %position-relative 
  z-index: 1
  &::before 
    @extend %position-absolute 
    content: '' 
    top: 34% 
    width: 15px 
    height: 15px 
    @extend %bg-grad 
    @extend %bs-50 
    right: -18px
    z-index: -1
    +media(1399px) 
      right: -45px 
      top: 31% 
    +media(767px)
      right: -40px 
  &::after 
    @extend %position-absolute 
    content: '' 
    top: 38% 
    width: 34px 
    height: 2px 
    // background-color: rgba(#fff, 0.25) 
    @extend %bg-grad 
    @extend %bs-50 
    right: -13px
    z-index: -2
    +media(1399px)  
      width: 39px 
      top: 36%
    +media(767px)
      width: 35px 
  .referral_circle 
    @extend %position-relative 
    width: 150px
    height: 150px 
    margin-bottom: rem(15px) 
    z-index: 1
    +media(1399px) 
      width: 110px 
      height: 110px 
    .referral_circle_inner 
      position: relative
      @extend %bg-grad 
      @extend %bs-50 
      @extend %w-100 
      @extend %h-100 
      display: inline-flex 
      flex-flow: column 
      @extend %align-center 
      @extend %justify-center 
      z-index: 1
      &::before 
        @extend %position-absolute 
        content: '' 
        top: 3px
        left: 3px
        width: calc(100% - 6px)
        height: calc(100% - 6px)
        // @extend %w-100
        // @extend %h-100
        // @extend %bg-grad
        background-color: $base-color-two 
        @extend %bs-50 
        z-index: -1
      &::after 
        @extend %position-absolute 
        content: '' 
        top: 50% 
        right: -16px
        width: 28px
        height: 52px 
        +transform(translateY(-50%))
        @extend %bg-grad 
        clip-path: polygon(0 0, 70% 0%, 100% 50%, 70% 100%, 0 100%, 0% 50%)
        z-index: -2
      span 
        font-weight: 600 
        font-size: rem(42px) 
        line-height: 1 
        color: #fff 
        +media(1399px)
          font-size: rem(32px) 
      p 
        @extend %text-base 
/* referral section css end */