/* banner section css start */
.sp_banner
  padding-top: rem(180px)
  padding-bottom: rem(200px)
  @extend %position-relative
  @extend %z-index-p 
  @extend %overflow-hidden 
  +media(1199px) 
    padding-top: rem(100px)
    padding-bottom: rem(100px) 
  +media(991px) 
    padding-top: rem(70px) 
  &_el 
    @extend %position-absolute 
    bottom: 70px 
    @extend %w-100 
    @extend %z-index-c 
    opacity: 0.1 
  .banner-left
    @extend %position-absolute 
    top: 0 
    left: -100px 
    @extend %z-index-c 
    opacity: 0.15
  .container
    padding-top: rem(70px) 
  .sp_banner_title 
    font-size: rem(66px) 
    background-image: linear-gradient(88deg, #b21ffd 0%, #67e3ff 100%)
    -webkit-background-clip: text 
    -webkit-text-fill-color: transparent 
    +media(1399px) 
      font-size: rem(52px) 
    +media(1199px) 
      font-size: rem(42px) 
    +media(767px) 
      font-size: rem(36px) 

.sp_banner_token_wrapper 
  @extend %position-relative 
  width: 450px 
  height: 450px 
  background-color: $body-bg
  @extend %bs-50 
  margin-left: auto
  z-index: 1
  background-size: 450px 450px
  @extend %z-index-p
  +media(1399px)
    width: 350px 
    height: 350px 
  +media(991px)
    width: 250px 
    height: 250px 
    margin-right: auto 
    margin-top: rem(120px) 
  .circle1 
    @extend %position-absolute 
    top: -50px 
    left: -50px 
    width: calc(100% + 100px) 
    height: calc(100% + 100px) 
    background-color: rgba($base-color, 0.1)
    @extend %bs-50 
    @extend %z-index-c 
    animation-name: rotateme
    animation-duration: 20s
    animation-iteration-count: infinite
    animation-timing-function: linear
    +media(991px) 
      top: -25px 
      left: -25px 
      width: calc(100% + 50px) 
      height: calc(100% + 50px) 
    &::before 
      @extend %position-absolute 
      content: '' 
      top: -6px 
      left: 50%
      width: 12px 
      height: 12px 
      background-color: #ff7cb3 
      @extend %bs-50 
      z-index: 1
      border: 2px solid #fff
    &::after  
      @extend %position-absolute 
      content: '' 
      bottom: -6px 
      left: 50%
      width: 12px 
      height: 12px 
      background-color: #FFC107 
      @extend %bs-50 
      z-index: 1
      border: 2px solid #fff
  .circle2
    @extend %position-absolute 
    top: -100px 
    left: -100px 
    width: calc(100% + 200px) 
    height: calc(100% + 200px) 
    background-color: rgba($base-color, 0.1)
    @extend %bs-50 
    @extend %z-index-c
    animation-name: rotatemetwo
    animation-duration: 20s
    animation-iteration-count: infinite
    animation-timing-function: linear
    +media(991px) 
      top: -50px 
      left: -50px 
      width: calc(100% + 100px) 
      height: calc(100% + 100px) 
    &::before 
      @extend %position-absolute 
      content: '' 
      top: 50% 
      left: -6px
      width: 12px 
      height: 12px 
      background-color: #ff7cb3 
      @extend %bs-50 
      z-index: 1
      border: 2px solid #fff
    &::after  
      @extend %position-absolute 
      content: '' 
      right: -6px 
      top: 50%
      width: 12px 
      height: 12px 
      background-color: #FFC107 
      @extend %bs-50 
      z-index: 1
      border: 2px solid #fff
  .ping-pong1 
    @extend %position-absolute 
    top: -45px 
    left: -45px 
    width: calc(100% + 90px) 
    height: calc(100% + 90px) 
    background-color: transparent
    @extend %bs-50 
    @extend %z-index-c 
    animation-name: rotateme
    animation-duration: 20s
    animation-iteration-count: infinite
    animation-timing-function: linear
    &::before 
      @extend %position-absolute 
      content: '' 
      top: 50%
      left: -6px 
      width: 12px 
      height: 12px 
      background-color: #48DDA9 
      @extend %bs-50 
      z-index: 1
      border: 2px solid #fff
  .ping-pong2
    @extend %position-absolute 
    top: -100px 
    left: -100px 
    width: calc(100% + 200px) 
    height: calc(100% + 200px) 
    background-color: transparent
    @extend %bs-50 
    @extend %z-index-c
    animation-name: rotatemetwo
    animation-duration: 20s
    animation-iteration-count: infinite
    animation-timing-function: linear
    &::before 
      @extend %position-absolute 
      content: '' 
      top: -6px 
      left: 50%
      width: 12px 
      height: 12px 
      background-color: #48DDA9 
      @extend %bs-50 
      z-index: 1
      border: 2px solid #fff

.token_sold, 
.token_goal 
  @extend %position-absolute 
  padding: rem(15px) 
  @extend %bs-8 
  display: flex 
  @extend %align-center 
  animation: upDown 3s infinite linear
  +media(991px) 
    padding: rem(10px) 
  .token_sold_icon 
    width: 50px 
    height: 50px 
    background-color: #FFC107 
    @extend %bs-50 
    @extend %d-flex 
    @extend %align-center 
    @extend %justify-center 
    color: #fff 
    +media(991px)  
      width: 35px 
      height: 35px 
    i 
      font-size: rem(22px)
  .token_sold_content
    width: calc(100% - 50px)
    padding-left: rem(15px) 
    +media(991px)  
      width: calc(100% - 35px) 
      padding-left: rem(10px)
    .token_title 
      +media(991px)
        font-size: rem(16px) 
    p 
      color: #fff 
      +media(991px) 
        font-size: rem(14px) 

.token_sold 
  top: -100px 
  right: -130px 
  background-image: linear-gradient(88deg, #902cfd 0%, rgb(35 69 167) 100%)
  animation-delay: 0.5s
  +media(1600px) 
    right: 0 
  +media(991px)
    top: -70px 
  &::before 
    @extend %position-absolute 
    content: '' 
    top: 50% 
    left: -180px 
    width: 180px 
    height: 60px
    border-top: 1px solid rgba(#fff, 0.15)
    border-left: 1px solid rgba(#fff, 0.15)
    +border-radius(8px 0 0 0)
    +media(1600px)  
      left: -65px 
      width: 65px 
    +media(1399px)
      left: -30px 
      width: 30px 
.token_goal 
  bottom: -100px 
  left: -130px 
  background-image: linear-gradient(88deg, rgb(58, 71, 171) 0%, rgb(77, 191, 217) 100%)
  +media(1600px) 
    left: 0 
  +media(991px)
    bottom: -70px 
  &::before 
    @extend %position-absolute 
    content: '' 
    top: -50% 
    right: -180px 
    width: 180px 
    height: 60px
    margin-top: 20px
    border-bottom: 1px solid rgba(#fff, 0.15)
    border-right: 1px solid rgba(#fff, 0.15)
    +border-radius(0 0 8px 0)
    +media(1600px)  
      right: -75px 
      width: 75px 
    +media(1399px) 
      right: -30px 
      width: 30px 
    +media(991px) 
      right: -20px 

+keyframes(upDown) 
  0%
    +transform(translateY(0))
  25%
    +transform(translateY(-5px))
  50%
    +transform(translateY(-15px))
  75%
    +transform(translateY(-5px))
  100%
    +transform(translateY(0))


+keyframes(rotateme) 
  0%
    +transform(rotate(0deg))
  25%
    +transform(rotate(90deg))
  50%
    +transform(rotate(180deg))
  100%
    +transform(rotate(360deg))

+keyframes(rotatemetwo) 
  0%
    +transform(rotate(0deg))
  25%
    +transform(rotate(-90deg))
  50%
    +transform(rotate(-180deg))
  100%
    +transform(rotate(-360deg))
    

#main-clock 
  @extend %d-flex 
  width: 450px 
  margin: 0 rem(-10px)
  +media(991px) 
    margin-left: auto 
    margin-right: auto 
  +media(575px) 
    width: 100% 
  .single-time
    width: 25%
    padding: 0 rem(10px) 
    @extend %position-relative 
    display: flex
    
    &:last-child
      &::before,
      &::after 
        display: none 
    &::before,
    &::after 
      @extend %position-absolute
      content: '' 
      right: 10px
      width: 6px
      height: 6px 
      @extend %bg-white 
      @extend %bs-50 
      opacity: 0.45 
    &::before  
      top: 18% 
    &::after 
      top: calc(21% + 20px) 
    .single-time-inner 
      text-align: center 

    p 
      @extend %text-white 
      margin-top: rem(6px)
      +media(1399px)  
        font-size: rem(14px) 
    span 
      font-weight: 400  
      // background-color: transparent 
      @extend %text-white 
      font-size: rem(28px)

      @extend %position-relative
      width: 65px 
      height: 65px 
      margin-left: auto
      margin-right: auto
      border: 1px solid rgba(#fff, 0.15) 
      @extend %bs-5
      @extend %d-flex 
      @extend %align-center 
      @extend %justify-center 
      @extend %base-color-two 
      // background: rgba(255, 255, 255, 0.1)
      // backdrop-filter: blur(6.5px)
      // -webkit-backdrop-filter: blur(6.5px)
      z-index: 1
      &::before 
        @extend %position-absolute 
        content: '' 
        top: 0
        left: 0
        @extend %w-100
        @extend %h-100
        @extend %bg-grad
        @extend %bs-5
        z-index: -2
      &::after 
        @extend %position-absolute 
        content: '' 
        top: 1px 
        left: 1px
        width: calc(100% - 2px)
        height: calc(100% - 2px)
        @extend %base-color-two 
        @extend %bs-5
        z-index: -1

      +media(1399px)  
        font-size: rem(28px)
  .main-clock-title 
    +media(1399px)
      font-size: rem(18px) 

.token-status 
  position: absolute
  top: 50% 
  left: 50% 
  padding: 0 40px 
  width: 100%
  +transform(translate(-50%, -50%))
  .token-status-amount 
    display: flex
    flex-flow: column
    align-items: center 
    justify-content: center 
  .token-status-progress 
    width: 100% 
    margin: 30px 0

  .top-text 
    @extend %d-flex 
    justify-content: space-between
    margin-bottom: 5px 
    height: 25px 
    @extend %position-relative 
    span  
      @extend %position-absolute 
      top: 0 
      white-space: nowrap
      font-size: rem(13px) 
      +transform(translateX(-100%)) 
      &::after 
        @extend %position-absolute 
        content: ''
        top: 90%
        left: 50% 
        width: 1px 
        height: 39px 
        @extend %bg-white 
        opacity: 0.15
  .progress 
    +border-radius(999px) 
    height: 20px 
    background-color: rgba(#fff, 0.05)  
    border: 1px solid rgba(#fff, 0.1)
    padding: rem(3px) 
    .progress-bar 
      @extend %base-color 
      +border-radius(999px) 

.sp_token_wave 
  position: absolute 
  width: 100% 
  height: 100% 
  left: 50% 
  top: 50% 
  transform: translate(-50%,-50%) 
  border-radius: 10px 
  box-shadow: 0px 0px 20px #000 
  background-color: transparent 
  overflow: hidden 

.wave-01 
  position: absolute 
  width: 300% 
  height: 300% 
  left: -100% 
  top: 50% 
  background-color: $base-color
  opacity: 0.1
  border-radius: 45% 
  animation: rotate 15 linear infinite 
.wave-02 
  position: absolute 
  width: 300% 
  height: 300% 
  left: -100% 
  top: 57% 
  background-color: darken($base-color, 5%)
  opacity: 0.1
  border-radius: 43% 
  animation: rotate 10s linear infinite 
.wave-03 
  position: absolute 
  width: 300% 
  height: 300% 
  left: -100% 
  top: 60% 
  background-color: darken($base-color, 10%)
  opacity: 0.1 
  border-radius: 40% 
  animation: rotate 7s linear infinite 

+keyframes (rotate) 
  100%
    transform: rotate(360deg)
/* banner section css end */

/* page banner css start */
.page-banner 
  padding-top: rem(180px)
  padding-bottom: rem(60px) 
  @extend %position-relative 
  @extend %z-index-p
  @extend %base-color-two 
  &::before
    @extend %position-absolute 
    content: '' 
    top: 0 
    left: 0 
    @extend %w-100 
    @extend %h-100 
    @extend %base-color-two 
    opacity: 0.75 
    @extend %z-index-c

.page-breadcrumb 
  @extend %d-flex
  @extend %align-center 
  li 
    @extend %text-white 
    padding: 0 rem(10px)
    @extend %position-relative 
    &:last-child
      &::after 
        display: none
    &::after 
      @extend %position-absolute 
      content: '/'
      top: 0 
      right: rem(-3px) 
    a 
      @extend %text-base 



.curved-container 
  position: absolute
  width: 200px
  height: 220px
  transform: translateX(-50%) rotate(-45deg)
  -moz-transform: translateX(-50%) rotate(-45deg)
  -webkit-transform: translateX(-50%) rotate(-45deg)
  top: -67px
  left: 47%

.curved-container h2 
  border: none

.curved-container span 
  font-size: rem(18px)
  font-weight: 600 
  height: 220px
  position: absolute
  width: 20px
  left: 0
  top: 0
  transform-origin: bottom center
  -moz-transform-origin: bottom center
  -webkit-transform-origin: bottom center

