/* about section css start */ 
.sp_about_section 
  @extend %position-relative 
  @extend %z-index-p
  .sp_about_section_wave 
    @extend %position-absolute 
    bottom: 0 
    left: 0
    @extend %w-100 
    opacity: 0.15 
    @extend %z-index-c 

.sp_about_content
  &_sub_title 
    line-height: 1.6 
    font-size: rem(17px) 

.sp_about_list 
  li + li 
    margin-top: rem(15px) 
  li 
    @extend %position-relative 
    padding-left: 25px
    @extend %text-white 
    &::before 
      @extend %position-absolute 
      content: '' 
      top: 9px 
      left: 0 
      width: 10px 
      height: 10px 
      @extend %bg-grad
      @extend %bs-50 

.sp_about_item
  padding: rem(30px)
  @extend .sp_separator_bg 
  @extend %bs-10 
  border: 1px solid rgba(#fff, 0.1)
  @extend %transition 
  &:hover 
    border-color: $base-color 
    @extend %trans-y 
  &_icon 
    width: 80px
    height: 80px 
    @extend %bg-grad 
    @extend %text-white 
    @extend %d-inline-flex 
    @extend %justify-center 
    @extend %align-center 
    @extend %bs-50 
    i 
      font-size: rem(42px)
  &_title 
    margin-top: rem(30px)
    margin-bottom: rem(15px)
  .circle-arrow
    margin-top: rem(30px)

  
.circle-arrow 
  width: 40px
  height: 40px
  @extend %text-white 
  border: 1px solid $base-color 
  @extend %d-inline-flex 
  @extend %align-center 
  @extend %justify-center 
  @extend %bs-50 
  font-size: rem(18px) 
  @extend %transition 
  &:hover 
    @extend %base-color 
    @extend %text-white 
/* about section css end */ 