/* token section css start */
.sp_token_wrapper
  @extend %d-flex 
  @extend %align-center 
  +media(991px)
    justify-content: center
  .sp_chart_area
    width: 600px
    @extend %position-relative 
    +media(1399px) 
      width: 500px 
    &_title 
      @extend %position-absolute 
      top: 39%
      left: 50% 
      +transform(translateX(-50%)) 
      +media(1399px) 
        position: static 
        +transform(translateX(0)) 
        text-align: center 
  .token_allocation_wrapper
    width: calc(100% - 600px)
    padding-left: rem(50px) 
    +media(1399px)  
      width: calc(100% - 500px)
    +media(1199px)
      padding-left: rem(20px)
    +media(991px)
      width: 100% 
      margin-top: 45px 
    .token_allocation_list
      padding: rem(35px) rem(50px) 
      @extend .sp_separator_bg 
      margin: rem(-15px)
      @extend %d-flex 
      @extend %bs-10 
      +media(1199px) 
        padding: rem(25px) rem(20px) 
        margin: 0 
      li 
        padding: rem(10px) 
        width: 50%
        padding-left: rem(30px) 
        @extend %position-relative 
        &::before 
          @extend %position-absolute 
          content: '' 
          top: 19px 
          left: 0 
          width: 15px 
          height: 15px 
          @extend %base-color 
          @extend %bs-50 
          box-shadow: 0 0 3px 1px rgba($base-color, 0.45) 
        &::after  
          @extend %position-absolute 
          content: '' 
          top: 21px 
          left: 2px 
          width: 11px 
          height: 11px 
          @extend .sp_separator_bg 
          @extend %bs-50 
        p 
          font-weight: 500 
          @extend %text-white 
          font-size: rem(18px) 
          +media(1199px)
            font-size: rem(16px)
        span 
          @extend %text-base 
          +media(1199px)
            font-size: rem(14px)

#chart 
  .apexcharts-legend-text 
    color: #fff !important 
    font-size: rem(16px) !important
  .apexcharts-legend-series 
    margin: 5px 10px !important
  .apexcharts-canvas 
    +media(1399px)
      margin-left: auto
      margin-right: auto 
  
/* token section css end */