/* account page css start */
.account-page 
  @extend %d-flex 
  min-height: 100vh 
  .form-wrapper 
    width: 40%
    padding: rem(30px) rem(50px)
    display: flex 
    flex-flow: column 
    justify-content: space-between 
    +media(575px)
      padding: rem(30px) rem(20px)
    +media(1199px)
      width: 50%
    +media(991px)
      width: 100%
    .inner-wrapper 
      padding: rem(50px) 0 
  .copy-right-text 
    @extend %text-center 
  .img-wrapper
    @extend %position-relative 
    width: 60% 
    @extend %base-color-two 
    @extend %z-index-p
    display: flex 
    flex-flow: column 
    @extend %align-center 
    @extend %justify-center  
    +media(1199px)
      width: 50%
    +media(991px)
      display: none 
    .account-img 
      max-height: 700px 
      +media(1399px) 
        max-height: 550px 
    .account-line-bg 
      @extend %position-absolute 
      top: 50% 
      left: 50% 
      +transform(translate(-50%, -50%))
      @extend %z-index-c
  &.registration-page 
    .form-wrapper
      +media(1399px)
        width: 50%
      +media(1199px)
        width: 55%
      +media(991px)
        width: 100%
    .img-wrapper 
      +media(1399px)
        width: 50%
      +media(1199px)
        width: 45%

/* account page css end */