/* card css start */
.custom--card 
  @extend %base-color-two 
  border: 1px solid $border-color
  @extend %bs-8
  overflow: hidden
  .card-header 
    padding: rem(15px) rem(20px) 
    border-color: $border-color 
  .card-body 
    padding: rem(20px)
    +media(575px)
      padding: rem(15px)
/* card css end */ 